.carousel-root {
  width: 84% !important;
  margin: auto !important;
  margin-top: 3% !important;
}

.carousel .slide {
  background: rgb(255, 255, 255) !important;
  color: rgb(0, 0, 0);
  height: 100%;
}

.carousel .slide img {
  width: 139px !important;
  border-radius: 50%;
}

.myCarousel {
  background: #ffffff;
  margin-top: -6%;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  border: 1px solid rgb(255, 255, 255);
  height: 286px;
}

.carousel .control-dots {
  padding-left: 5px !important;
  outline: 0;
  bottom: 5% !important;
}

.myCarousel h3 {
  color: rgb(0, 0, 0);
  font-weight: 100;
  letter-spacing: 0.2px;
  font-family: "Open Sans";
  font-weight: 600;
  font-size: 25px;
}

.myCarousel h4 {
  text-transform: uppercase;
  margin-top: 0;
  padding-top: 0;
  font-weight: 500;
  color: #787878;
  font-size: 14px;
}

.myCarousel p {
  font-weight: 400 !important;
  line-height: 29px !important;
  color: #222;
  font-size: 15px;
  font-family: "Open Sans";
  max-height: 67px;
}

.myCarousel p:before {
  content: "“";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
}

.myCarousel p:after {
  content: "”";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
  line-height: 0;
}

.carousel .control-dots .dot {
  box-shadow: none !important;
  background: #000000 !important;
  outline: 0;
}

.carousel.carousel-slider .control-arrow {
  background: #000 !important;
  height: 50px !important;
  position: absolute;
  top: 35% !important;
}

.App {
  text-align: center;
}

@media only screen and (max-width: 934px) {
  .carousel-root {
    outline: 0;
    width: 93% !important;
    margin: auto !important;
  }

  .carousel.carousel-slider .control-arrow {
    display: none !important;
  }
  .myCarousel {
    margin-top: -9%;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    padding-bottom: 12.5%;
    padding-left: 5%;
    padding-right: 5%;
    height: 269px;
  }

  .carousel .slide img {
    width: 24% !important;
    border-radius: 50%;
  }
}

@media screen and (max-width: 480px) {

  .myCarousel h3 {
    font-size: 20px;
  }

  .myCarousel p {
    line-height: 24px !important;
    font-size: 12px;
    max-height: 60px;
  }

  .myCarousel p:before {
    font-size: 21px;
  }
  
  .myCarousel p:after {
    font-size: 21px;
  }
  
}